/**
 * Popup.scss
 * SCSS for custom popup
 *
 * @version 0.0.1
 * @author [Jiahao](jiahao@policypal.com)
 */

$padding-horizontal: 58px;
$padding-vertical: 36px;
$closeIconPadding: 16px;

.popup {
  z-index: 1500;
  height: 100vh;
  width: 100vw;
  position: fixed;
  overflow: hidden;
  display: flex;
  justify-content: center;
  top: 0;
  left: 0;
  top: 0;
    left: 0;
    right: 0;
    bottom: 0;

  &::before {
    background-image: linear-gradient(134deg, rgba(0, 215, 175, 0.75), rgba(70, 115, 122, 0.75));
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
    content: '';
    height: 100vh;
    left: 0;
    position: fixed;
    top: 0;
    width: 100vw;
    will-change: transform;
    z-index: -1;
  }

  &.blue-popup-backdrop {
    &:before {
      background-image: linear-gradient(150.14deg, #6480EB 0.6%, #433BC4 100%) !important;
      opacity: 0.9 !important;
    }
  }

  .cont {
    display: inline-block;
    position: relative;
    border-radius: 8px;
    padding: $padding-vertical 0 0 0;
    box-shadow: 0 30px 40px 0 rgba(0, 0, 0, 0.15);
    background: #ffffff;
    display: flex;

    @media screen and (max-width: 576px) {
      overflow-y: auto;
      overflow-x: hidden;
      height: 100%;
      width: 100%;
    }

    @media screen and (min-width: 576px) {
      padding: $padding-vertical $padding-horizontal;
      overflow-y: auto;
      max-height: calc(100vh - 48px - 36px) !important;
      max-width: calc(100vw - 48px);
      margin: 48px 24px auto;
    }

    @media screen and (min-width: 1200px) {
      padding: 60px 98px;
    }

    .close-icon {
      position: absolute;
      right: $closeIconPadding;
      top: $closeIconPadding;
      width: 24px;
      filter: brightness(0) saturate(100%) invert(95%) sepia(7%) saturate(540%) hue-rotate(143deg) brightness(96%) contrast(85%);

      &:hover {
        transition: all 0.3s ease;
        transform: scale(1.1);
        filter: brightness(0) saturate(100%) invert(54%) sepia(86%) saturate(1621%) hue-rotate(128deg) brightness(103%) contrast(101%);
      }
    }
  }

  .cont-opened {
    transition: transform 0.5s ease;
    opacity: 1;
    transform: scale(1);
  }

  .cont-closed {
    transition: transform 0.5s ease;
    opacity: 0;
    transform: scale(0);
  }
}

.popup-opened {
  transition: opacity 0.5s ease;
  opacity: 100;
  z-index: 9999;
  transform: none;
}

.popup-closed {
  transition: opacity 0.5s ease;
  opacity: 0;
  z-index: -9999;
  transform: translateX(-9999px);
}